import React from 'react';
import windowSize from 'react-window-size';

class SegmentedController extends React.Component {

    render() {
        

        if (this.props.windowWidth < 480) {
            return (
                <div class="m-n-segmented-controller-container">
                    <div class="m-n-segmented-controller">
                        <div class={"m-n-tab-button"}
                            onClick={() => { this.props.switchMode('top') }}>
                            <div class="m-n-tab-button-text">Top</div>
                        </div>
                        <div class={ "m-n-tab-button"} onClick={() => this.props.switchMode('new')} >
                            <div class="m-n-tab-button-text">New</div>
                        </div>

                        <div class={ "m-n-tab-button"} onClick={() => this.props.switchMode('weekly')} >
                            <div class="m-n-tab-button-text">Weekly</div>
                        </div>
                        
                    </div>
                </div>
            )
        } else {
            return null;
        }

    }
}

export default windowSize(SegmentedController);